class TooltipContainerElement extends HTMLElement {
    constructor() {
        super();
        this.globalEscape = this.globalEscape.bind(this);
    }
    connectedCallback() {
        const trigger = this.querySelector('[aria-describedby]') || this.querySelector('[aria-labelledby]');
        const tooltip = this.querySelector('[role="tooltip"]');
        if (!trigger || !tooltip)
            return;
        this.addEventListener('mouseover', (event) => {
            this.open(event);
        });
        this.addEventListener('mouseleave', (event) => {
            this.close(event);
        });
        trigger.addEventListener('focus', (event) => {
            this.open(event);
        });
        trigger.addEventListener('blur', (event) => {
            this.close(event);
        });
    }
    open(event) {
        showTooltip(event, this);
        document.addEventListener('keydown', this.globalEscape);
    }
    close(event) {
        hideTooltip(event, this);
        document.removeEventListener('keydown', this.globalEscape);
    }
    globalEscape(event) {
        if (event.key === 'Escape' || event.key === 'Esc') {
            closeTooltip(this);
        }
    }
}
